// start content

$green: #08a057 !default;
$blue: #3d61fd !default;

@import "bootstrap/bootstrap";

@import url("https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard-dynamic-subset.css");
@import url("https://fonts.googleapis.com/css?family=Nanum+Gothic");
@import url("https://fonts.googleapis.com/icon?family=Material+Icons+Sharp");

html {
  scroll-behavior: smooth;
  overflow-y: scroll;
}

:root[color-theme="light"] {
  --background: #fff;
  --font-color: #333;
  --font-color-l: #000000;
  --nav-link-color: rgb(109, 109, 109);
  --article-color: rgba(0, 0, 0, 0.8);
  --blockquote-color: rgba(0, 0, 0, 0.5);
  --codeblock-color: #f5f5f5bb;
  --codeblock-highlight-color: #586e75;
  --nav-scroll-shadow: rgba(0, 0, 0, 0.05);
  --title-text-shadow: 3px 4px 10px rgba(255, 255, 255, 0.6);
}

:root[color-theme="dark"] {
  --background: rgb(13, 13, 14);
  --font-color: #d2d2d2;
  --font-color-l: #fff;
  --nav-link-color: rgb(186, 186, 186);
  --article-color: rgba(255, 255, 255, 0.8);
  --blockquote-color: rgba(255, 255, 255, 0.5);
  --codeblock-color: #191c1c;
  --codeblock-highlight-color: #a3b8bf;
  --nav-scroll-shadow: rgb(255, 255, 255, 0.1);
  --title-text-shadow: 3px 4px 10px rgba(255, 255, 255, 0.1);
}

body {
  background: var(--background);
  font-size: 1rem;
  // transition: all 0.0002ms linear;
  font-family: Pretendard, -apple-system, BlinkMacSystemFont, system-ui, Roboto,
    "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", sans-serif;
  font-weight: 500;
  line-height: 1.8;
  color: var(--font-color);
  overflow-x: hidden;
  padding-top: 90px;
  // transition: background 1ms ease-in, color 1ms ease-in;
}

.serif-font {
  font-family: PT Serif;
}
@media (max-width: 768px) {
  body {
    padding-top: 60px;
  }
  .container-xl,
  .container-lg,
  .container-md,
  .container-sm,
  .container,
  .utterances {
    width: 93%;
  }
  .stress {
    position: absolute;
    top: 40%;
    text-align: left;
    font-size: 3rem;
    color: var(--font-color-l);
    font-weight: 700;
    line-height: 1.5;
    text-shadow: var(--title-text-shadow);
  }
  section {
    margin-top: 3.5rem;
  }

  .item {
    padding-bottom: 0.5rem;
  }

  .company {
    padding-top: 1rem;
  }
}

@media (min-width: 769px) {
  .container-xl,
  .container-lg,
  .container-md,
  .container-sm,
  .container,
  .utterances {
    padding: 0 8rem;
  }
  .stress {
    position: absolute;
    top: 45%;
    text-align: left;
    font-size: 3.5rem;
    color: var(--font-color-l);
    font-weight: 700;
    line-height: 1.5;
    text-shadow: var(--title-text-shadow);
  }
  section {
    margin-top: 5rem;
  }

  .item {
    padding-bottom: 1.5rem;
  }
}

@media (min-width: 993px) {
  .container-xl,
  .container-lg,
  .container-md,
  .container-sm,
  .container,
  .utterances {
    padding: 0 14rem;
    // .content {
    //   width: 75%;
    // }
  }
  .stress {
    position: absolute;
    top: 40%;
    text-align: left;
    font-size: 5rem;
    color: var(--font-color-l);
    font-weight: 700;
    line-height: 1.5;
    text-shadow: var(--title-text-shadow);
  }
  section {
    margin-top: 5rem;
  }

  .item {
    padding-bottom: 1.5rem;
  }
}

@media (min-width: 2000px) {
  .container-xl,
  .container-lg,
  .container-md,
  .container-sm,
  .container,
  .utterances {
    padding: 0 13rem;
    // .content {
    //   width: 70%;
    // }
  }
  .stress {
    position: absolute;
    top: 40%;
    text-align: left;
    font-size: 5rem;
    color: var(--font-color-l);
    font-weight: 700;
    line-height: 1.5;
    text-shadow: var(--title-text-shadow);
  }
  section {
    margin-top: 5rem;
  }

  .item {
    padding-bottom: 1.5rem;
  }
}

