/* #Navigation
================================================== */

.start-header {
  opacity: 1;
  transform: translateY(0);
  padding: 10px 0;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.start-header.scroll-on {
  padding: 5px 0;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  box-shadow: 0px 10px 40px 0px var(--nav-scroll-shadow);
}
.start-header.scroll-on .navbar-brand img {
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.navigation-wrap {
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  position: fixed;
  background: var(--background);
}
.social-icon {
  display: inline-block !important;
  width: 20px;
  height: 20px;
  margin-left: 15px;
}

/* Sidebar
================================================== */
.sidebar.sticky-top {
  top: 96px;
  .dropdown-item {
    opacity: 0.6;
  }
  .dropdown-item.blog {
    display: none;
  }
}

